import { Injectable } from '@angular/core';

import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from 'angularfire2/firestore';
import { Attendees, Event } from '../models/common';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class EventService {

  eventsCollection: AngularFirestoreCollection<Event>;
  events: Observable<Event[]>;
  eventDoc: AngularFirestoreDocument<Event>;
  visitID: string;

  constructor(public afs: AngularFirestore) {
  	// this.eventsCollection = this.afs.collection('events');
    // //this.events = this.eventsCollection.valueChanges();
    // this.events = this.eventsCollection.snapshotChanges().map(changes => {
    //   return changes.map(a => {
    //     const data = a.payload.doc.data() as Event;
    //     data.id = a.payload.doc.id;
    //     return data;
    //   });
    // });
  }

  get() {
    //return this.events;
  }

  getVisitEvents(visit: string) {
  	this.eventsCollection = this.afs.collection('items', ref => {
      return ref.where('visitId', '==', visit).orderBy('orderby', 'asc');
    });
  	return this.eventsCollection.snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as Event;
        data.id = a.payload.doc.id;
        return data;
      });
    });
  }

  add(event: Event) {
    this.eventsCollection.add(event);
  }


}



