import { Injectable } from '@angular/core';

import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from 'angularfire2/firestore';
import { People } from '../models/common';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class PeopleService {

  peopleCollection: AngularFirestoreCollection<People>;
  people: Observable<People[]>;
  peopleDoc: AngularFirestoreDocument<People>;

  constructor(public afs: AngularFirestore) { }
  peopleDetails: any;
  get() {
    this.peopleCollection = this.afs.collection('people');
    return this.peopleCollection.snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as People;
        data.id = a.payload.doc.id;
        return data;
      });
    });
  }

  add(people: People) {
    this.peopleCollection.add(people);
  }

  delete(people: People) {
    this.peopleDoc = this.afs.doc(`people/${people.id}`);
    this.peopleDoc.delete();
  }

  getDetails(peopleId: string) {
    console.log('PeopleService>>>', peopleId);
    return this.afs.doc(`people/${peopleId}`).valueChanges();
  }

}



