import { Injectable } from '@angular/core';

import {AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument} from 'angularfire2/firestore';
import {Item, Visit} from '../models/common';
import {Observable} from 'rxjs/Observable';

@Injectable()
export class AppService {

  itemsCollection: AngularFirestoreCollection<Item>;
  items: Observable<Item[]>;
  itemDoc: AngularFirestoreDocument<Item>;

  visitsCollection: AngularFirestoreCollection<Visit>;
  visits: Observable<Visit[]>;
  visitDoc: AngularFirestoreDocument<Visit>;

  constructor(public afs: AngularFirestore) {
    // this.items = this.afs.collection('items').valueChanges();
    this.itemsCollection = this.afs.collection('items', ref => ref.orderBy('title', 'asc'));
    this.items = this.itemsCollection.snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as Item;
        data.id = a.payload.doc.id;
        return data;
      });
    });

    this.visitsCollection = this.afs.collection('Visits', ref => ref.orderBy('title', 'asc'));
    this.visits = this.visitsCollection.snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as Item;
        data.id = a.payload.doc.id;
        return data;
      });
    });

  }
  getItem() {
    return this.items;
  }
  addItem(item: Item) {
    this.itemsCollection.add(item);
  }
  deleteItem(item: Item) {
    alert('inside service .. delete item method');
    // this.itemDoc = this.afs.doc('items/' + item.id);
    this.itemDoc = this.afs.doc(`items/${item.id}`);
    this.itemDoc.delete();
  }
  updateItem(item: Item) {
    this.itemDoc = this.afs.doc(`items/${item.id}`);
    this.itemDoc.update(item);
  }

  addVisit(visit: Visit) {
    this.visitsCollection.add(visit);
  }

}



